.navigation {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: auto;
    font-family: 'Roboto Condensed', sans-serif;
    border-top: 5px solid rgba(129, 193, 75, 0.9);
    border-bottom: 5px solid #b5b5b5;
    padding: 1rem;
}

.navigation ul {
    display: table;
    margin: 0 auto;
    width: 100%;
    height: 30;
    line-height: 30px;
}

.listItem {
    display: table-cell;
    margin: 0;
    white-space: nowrap;
    width: 1%;
}

.listItem a {
    text-decoration: none;
    font-size: large;
    color: gray;
}

.listItem a:hover {
    color: green;
    border-bottom: 3px solid green;
    border-radius: 5px;
}

.listItem_active {
    color: green;
    border-bottom: 3px solid green;
    border-radius: 5px;
}

.listItem-2 {
    list-style-type: none;
}

.listItem-2:hover {
    list-style-type: disc;
    color: red;
}

.listItem-2 a{
    color: green;
    text-decoration: none;
}

.listItem-2 a:hover {
    color: grey;
}

